import React from 'react';
import PropTypes from 'prop-types';
import Helmet from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';

function SEO({ description, lang, meta, title, logoImage, url }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            siteUrl
            keywords
            followAddress
            logoImage
            author {
              name
              email
            }
            socials {
              name
              url
            }
          }
        }
      }
    `
  );
  debugger;
  const metaDescription = description || site.siteMetadata.description;
  const logoImageUrl = logoImage || site.siteMetadata.logoImage;
  const siteUrl = url || site.siteMetadata.siteUrl;

  return (
    <Helmet
      htmlAttributes={{
        lang
      }}
      // bodyAttributes={{
      //   class: 'has-navbar-fixed-top'
      // }}
      title={title}
      titleTemplate={`%s | ${site.siteMetadata.title}`}
      meta={[
        {
          name: `description`,
          content: metaDescription
        },
        {
          property: `og:title`,
          content: title
        },
        {
          property: `og:url`,
          content: siteUrl
        },
        {
          property: `og:description`,
          content: metaDescription
        },
        {
          property: `og:type`,
          content: `website`
        },
        {
          name: `twitter:card`,
          content: `summary`
        },
        {
          name: `twitter:title`,
          content: title
        },
        {
          name: `twitter:site`,
          content: site.siteMetadata.followAddress
        },
        {
          name: `twitter:creator`,
          content: site.siteMetadata.followAddress
        },
        {
          name: `twitter:description`,
          content: metaDescription
        },
        {
          name: `twitter:image`,
          content: logoImageUrl
        },
        {
          name: `twitter:image:alt`,
          content: metaDescription
        },
        {
          name: `og:locale`,
          content: `en_US`
        }
      ].concat(meta)}
    />
  );
}

SEO.defaultProps = {
  lang: `en-US`,
  meta: [],
  description: ``
};

SEO.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired
};

export default SEO;
